/* main */
body {
  font-family: "HelveticaNeueCyr", "Helvetica", sans-serif;
  font-size: 18px;
  line-height: 21px;
  font-weight: 500;
  letter-spacing: 0.06em;
  color: #4d4d4d; }
  @media only screen and (max-width: 1900px) {
    body {
      font-size: 0.9375vw;
      line-height: 1.09375vw; } }
  @media only screen and (max-width: 767px) {
    body {
      font-size: 3.86473vw;
      line-height: 4.34783vw; } }

.main {
  overflow: hidden; }

.container {
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 15px; }
  @media only screen and (max-width: 1900px) {
    .container {
      max-width: 72.91667vw;
      padding: 0 0.78125vw; } }
  @media only screen and (max-width: 767px) {
    .container {
      max-width: none;
      padding: 0 4.83092vw; } }

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0; }

